.projectBlock {
    min-height: 100vh;
    background-color: forestgreen;
}

.projectContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.title {
    background-color: blueviolet;
}

.projects {
    flex-wrap: wrap;
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
}